

























































export default {
	data() {
		return {
			imageList: [],
			userInfo: {
				nickname: "未设置",
				mobile: "未绑定",
				head_img: "../../../static/head-no-pic.png",
				vip_name: "普通会员",
				group_name:"会员",
				is_validate: 0,
				realname: "",
			}
		}
	},
	onLoad() {},
	onShow() {
		this.getUserInfoData();
	},
	onShareAppMessage() {
		return {
			title: this.webshareTitle,
			path: '/pages/tabBar/index/index'
		}
	},
	onBackPress() {
	  uni.switchTab({
	  	url: '/pages/tabBar/user/user'
	  })
	},
	onNavigationBarButtonTap(e) {},
	methods: {
		getUserInfoData() {
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			try {
				this.$http.post(this.websiteUrl + '/api/api/getUserInfoData', {
					openid,
					sessionKey
				}, (res) => {
					if (res.data.code == 1) {
						this.userInfo = res.data.result.userInfo;
						this.imageList=[this.userInfo.head_img];
					}
				});
			} catch (e) {
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false
				})
			}
		},
		goDetailPage(url) {
			const openid = this.$store.state.openid;
			if (openid === null) {
				uni.navigateTo({
					url: '/pages/user/login/login'
				})
			} else {
				uni.navigateTo({
					url: url
				})
			}

		},
		chooseImage: async function() {
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			uni.chooseImage({
				sizeType: ['compressed'],
				count: 1,
				success: (res) => {
					this.imageList = [];
					this.imageList = this.imageList.concat(res.tempFilePaths);
					//console.log(res.tempFilePaths)
					const index = this.imageList.length;
					if(index>=1){
						this.userInfo.head_img = this.imageList[index-1];
						const tempFilePaths = res.tempFilePaths;
						uni.showLoading()
						try{
								uni.uploadFile({
									url: this.websiteUrl + '/api/upload/uploadface', //仅为示例，非真实的接口地址
									filePath: tempFilePaths[index-1],
									name: 'file',
									formData: {
										'data': JSON.stringify({openid,sessionKey})
									},
									success: (uploadFileRes) => {
										const data = JSON.parse(uploadFileRes.data);
										uni.hideLoading();
										if(data.code == 1){
											uni.showToast({
												title: "成功"
											})
										}else{
											uni.showToast({
												title: data.msg
											})
										}
									}
								});
						}catch(e){
							uni.hideLoading();
						}
					}else{
						uni.showToast({
							title: "没有选中图片"
						})
					}
					//console.log(this.imageList)
				}
			})
		},
		previewImage: function(e) {
			var current = e.target.dataset.src;
			 uni.showLoading()
			 uni.previewImage({
			 	current: current,
			 	urls: this.imageList.length>0?this.imageList:[current],
				complete:(res)=>{
					uni.hideLoading();
				}
			 })
		}
	}
}
